@import url("https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap");

html {
  font-family: "Roboto", sans-serif;
}

.card {
  width: 200px;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  padding: 32px 16px 16px;
  display: flex;
  flex-direction: column;
}

.card__picture {
  width: 160px;
  height: 134px;
  margin: 0 3px 40px;
}

.card__title {
  font-weight: 500;
  font-size: 12px;
  line-height: 18px;
  color: #060b35;
  margin-bottom: 4px;
}

.card__code {
  color: #616070;
  font-weight: 400;
  font-size: 10px;
  line-height: 14px;
  margin-bottom: 16px;
}

.card__raiting {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
}

.card__raiting-text {
  font-weight: 400;
  font-size: 10px;
  line-height: 14px;
  color: #060b35;
}

.card__pricing {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
}

.card__price {
  font-weight: 400;
  font-size: 12px;
  color: #616070;
  line-height: 18px;
}

.card__price-num {
  font-weight: 700;
  font-size: 16px;
  line-height: 18px;
  color: #060b35;
}

.card__buy-button {
  font-weight: 700;
  text-decoration: none;
  line-height: 16px;
  font-size: 14px;
  width: 166px;
  height: 40px;
  background: #00acdc;
  border-radius: 5px;
  text-transform: uppercase;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid #00acdc;
}

.card__buy-button:hover {
  background: #fff;
  color: #00acdc;
}
